import React, { useState, useEffect } from 'react'
import styled from 'styled-components'
import color from '../color-css/color'
import cursor from '../../assets/images/hand-point-up-1.png'
import { Spinner } from './Spinner'

export enum ButtonStates {
  disabled = 'disabled',
  enabled = 'enabled',
  progressing = 'progressing'
}

export type Props = {
  title: string,
  buttonStates: ButtonStates,
  onClickCallbackProp: () => void,
  onClickExecFunc?: () => any,
  onSuccessfulCallback?: (data: any) => void,
  onFailCallback?: (error: any) => void,
}

/**
 * enabled: 
 * disabled: 
 * progressing:
 */
const handleButtonStates = (getCurrentState: () => ButtonStates) => {
  switch (getCurrentState()) {
    case ButtonStates.enabled:
    default:
      return "background: #FF6820;";
    case ButtonStates.progressing:
      return "background: #FF6820;\n"
        + "cursor: not-allowed;\n"
        + "pointer-events: none;"
    case ButtonStates.disabled:
      return "background: #C9C9C9;\n"
        + "cursor: not-allowed;\n"
        + "pointer-events: none;"
  }
}

interface ProgressButton {
  onState: () => ButtonStates
}
const StyledProgressButton = styled.a<ProgressButton>`
  height: 3.125rem;
  width: 100%;
  display: block;
  text-align: center;
  line-height: 3.125rem;
  background: ${color.grey} 0% 0% no-repeat padding-box;
  border-radius: 6.25rem;
  opacity: 1;
  font-family: museo-sans, sans-serif;
  font-weight: 700;
  font-size: 1rem;
  letter-spacing: .8px;
  color: ${color.white};
  text-decoration: none;
  ${({ onState }) => handleButtonStates(onState)};
  cursor: url(${cursor}), auto;	
`

interface HoverProgressButton {
  onHoverButtonStates?: ButtonStates
}

const HoverProgressButton = styled.div<HoverProgressButton>`
  width: ${props => props.onHoverButtonStates === ButtonStates.progressing ? '13.875rem' : '11.125rem'};
  border-radius: 6.25rem;
  position: relative;
  &:hover ${StyledProgressButton} {
    background: ${props =>
    props.onHoverButtonStates === ButtonStates.enabled || props.onHoverButtonStates === ButtonStates.progressing
      ? "#FF6820" : "#C9C9C9"} ;
  }
`

interface ButtonSpinner {
  isShow: boolean
}
const ButtonSpinner = styled(Spinner) <ButtonSpinner>`
  display: ${props => props.isShow === true ? 'block' : 'none'};
  position: absolute;
  right: 1.3rem;
  margin-top: -2.1875rem;
`

const ProgressButton: React.FC<Props> = ({
  title,
  buttonStates,
  onClickCallbackProp,
  onClickExecFunc,
  onSuccessfulCallback,
  onFailCallback,
}) => {

  const [buttonState, updateButtonState] = useState(buttonStates)

  useEffect(() => {
    updateButtonState(buttonStates)
  }, [buttonStates, buttonState])

  return (
    <HoverProgressButton onHoverButtonStates={buttonStates}>
      <StyledProgressButton
        role='button'
        href='#'
        onState={() => {
          return buttonState
        }}
        onClick={event => {
          event.preventDefault()

          onClickCallbackProp()
          new Promise((resolve, reject) => {
            if (onClickExecFunc === undefined) {
              reject("Error: There is something wrong")
            }
            setTimeout(() => {
              resolve(onClickExecFunc ? onClickExecFunc() : undefined)
            }, 3000)

          }).then(result => {
            onSuccessfulCallback ? onSuccessfulCallback(result + " " + buttonState) : undefined
          }).catch(error => {
            onFailCallback ? onFailCallback(error) : undefined
          })

        }}
      >
        {title}
      </StyledProgressButton>
      {
        buttonStates === 'progressing'
          ? <ButtonSpinner isShow={true} className='' />
          : <ButtonSpinner isShow={false} className='' />
      }
    </HoverProgressButton>

  )
}

export default ProgressButton
