import React, { forwardRef, useEffect, useReducer } from 'react'
import styled from 'styled-components'
import ItemStep from './itemStep'

export type Props = {
  onItemStepClickedListenerProp?: (step: number) => void
  activateItemProp: number
}

const FormStepContainer = styled.div`
  display: flex;
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  border-radius: 0.3125rem;
  box-shadow: 0px 1.25rem 3.125rem #012F571A;
  padding: 2rem 3.125rem 1.9375rem;
  border: 1px solid #fff;
`

type Action = {
  type: string,
  payload: { [index: number]: boolean };
}

const reducerClickableIndex = (state: any, action: Action) => {
  switch (action.type) {
    case "itemAt" + 0:
      return {
        ...state,
        0: action.payload[0]
      };
    case "itemAt" + 1:
      return {
        ...state,
        1: action.payload[1]
      };
    case "itemAt" + 2:
      return {
        ...state,
        2: action.payload[2]
      };
    case "itemAt" + 3:
      return {
        ...state,
        3: action.payload[3]
      };

    default:
      return {
        ...state
      }
  }
}

const FormStep: React.FC<Props> = (props) => {

  const { onItemStepClickedListenerProp, activateItemProp } = props
  const [idActive, changeIdActive] = React.useState(0)

  const [clickableState, dispatch] = useReducer(reducerClickableIndex,
    { 0: true, 1: false, 2: false, 3: false })

  const activateItemAt = (index: number) => {
    changeIdActive(index)
    dispatch({
      type: "itemAt" + index,
      payload: {
        [index]: true
      }
    })
  }

  useEffect(() => {
    activateItemAt(activateItemProp)
  }, [activateItemProp])

  return (
    <FormStepContainer>
      <ItemStep
        idActive={idActive}
        id={0}
        title={'Your work'}
        changeIdActive={() => {
          changeIdActive(0)
          onItemStepClickedListenerProp ? onItemStepClickedListenerProp(0) : {}
        }}
        clickableProp={clickableState[0]}
      />

      <ItemStep
        idActive={idActive}
        id={1}
        title={'About you'}
        changeIdActive={() => {
          changeIdActive(1)
          onItemStepClickedListenerProp ? onItemStepClickedListenerProp(1) : {}
        }}
        clickableProp={clickableState[1]}
      />

      <ItemStep
        widthSize = '14rem'
        idActive={idActive}
        id={2}
        title={'Membership agreement'}
        changeIdActive={() => {
          changeIdActive(2)
          onItemStepClickedListenerProp ? onItemStepClickedListenerProp(2) : {}
        }}
        clickableProp={clickableState[2]}
      />

      <ItemStep
        widthSize = '11.1875rem'
        idActive={idActive}
        id={3}
        title={'Your contribution'}
        changeIdActive={() => {
          changeIdActive(3)
          onItemStepClickedListenerProp ? onItemStepClickedListenerProp(3) : {}
        }}
        clickableProp={clickableState[3]}
      />

    </FormStepContainer>
  )
}

export default FormStep
